.marquee {
  height: 70px;
  width: 100%;
  overflow: hidden;
  position: relative;
  font-size:52px;
  color:white;
  padding-bottom: 9px;
  cursor:pointer;
  /*border-bottom:1px solid white;*/
}
.marquee:hover {
  background: rgb(255,255,255);
    background: radial-gradient(rgba(145, 152, 229, 1) 0%, rgba(145, 152, 229, 0) 70%);
}

.marquee2 {
  font-family: "Traulha WEB Regulara",'NotoSerifKR';
  height: 70px;
  width: 100%;

  overflow: hidden;
  position: fixed;
  bottom: 0px;
  font-size:52px;
  color:white;
  padding-top: 10px;
  cursor:pointer;
}
.marquee2:hover {
  background: rgb(255,255,255);
  background: radial-gradient(rgba(145, 152, 229, 1) 0%, rgba(145, 152, 229, 0) 70%);
}

.marquee div {
  display: block;
  width: 200%;
  height: 80px;

  position: absolute;
  overflow: hidden;

  animation: marquee 5s linear infinite;
}

.marquee span {
  float: left;
  width: 50%;
}

.marquee2 div {
  display: block;
  width: 200%;
  height: 80px;

  position: absolute;
  overflow: hidden;

  animation: marquee 5s linear infinite;
}

.marquee2 span {
  float: left;
  width: 50%;
}

@keyframes marquee {
  0% { left: 0; }
  100% { left: -100%; }
}

