}

@keyframes logoFade {
    0% {
        display: inherit;
        opacity: 0;
    }

    100% {
        display: inherit;
        opacity: 1;
        
    }
}

@keyframes fadeInFromNone {
    0% {
        display: inherit;
        opacity: 1;
        pointer-events:inherit;
    }

    5%{
      pointer-events:none;
    }

    99% {
        display: inherit;
        opacity: 0.01;
    }

    100% {
        display: none;
        opacity: 0;
    }
}

